import { defineStore } from 'pinia'

export const dhRegistrationStore = defineStore("dh-registration", {
    state: () => ({
        subject: {
            age: null,
            sex: "",
            zip: null,
            firstName: "",
            lastName: "",
            dateOfBirth: "",
            phone: "",
            email: "",
            socialSecurity: "",
            signature: "",
            password: "",
            height: null,
            bodyweight: null,
            chd: null,
            myocardialInfarction: null,
            pacemaker: null,
            chronicLungDisease: null,
            chronicRenalDiease: null,
            vertigo: null,
            bleeding: null,
            dyspnoea: null,
            medications: [],
            smoking: null,
            cigerettesPerDay: 0,
            alcohol: null,
            drinksPerDay: null,
            cancer: null,
            regularlyActive: null,
            activityPerWeek: 0,
            cohort: null
        },
        criteria: {
            afib: null,
            anticoagulant: "",
            hypertension: null,
            heartFailure: null,
            angiopathy: null,
            stroke: null,
            diabetes: null,
        },
        step: -1,
        lockPhrase: "#2024@Adhp?"

    }),
    getters: {
        sexTranslated() {
            switch (this.subject.sex) {
                case 'male':
                    return 'M√§nnlich';
                case 'female':
                    return 'Weiblich';
                case 'diverse':
                    return 'Divers';
                default:
                    return 'nicht verf√ºgbar';
            }
        },
        osName() {
            if (window.navigator.userAgent.includes("iPhone") || window.navigator.userAgent.includes("iPad")) {
                return "ios";
            } else if (window.navigator.userAgent.includes("Android")) {
                return "android";
            } else if (window.navigator.userAgent.includes("Windows Phone")) {
                return "windowsPhone";
            } else if (window.navigator.userAgent.includes("Windows")) {
                return "windows";
            } else {
                return "unknown";
            }
        }
    },
    actions: {
        criteriaInterpreted(criteria) {
            switch (criteria) {
                case true:
                    return "Ja";
                case false:
                    return "Nein";
                case "none":
                    return "Keine";
                default:
                    return criteria;
            }
        }
    }
})