<template>
  <h2>Sie haben Post <font-awesome-icon icon="envelope" class="px-2" /></h2>
  <div class="text-center">
    Wir haben Ihnen einen Code per Best√§tigungsmail auf diese Addresse
    geschickt: <br /><br /><b>{{ store.subject.email }}</b> <br /><br />
    Bitte geben Sie den <b>6-stelligen Code</b> in dieser Email hier ein:
    <form class="needs-validation" @submit.prevent="checkCode" novalidate>
      <div class="row justify-content-center">
        <div class="code-form mt-4 mb-4 w-75">
          <input
            type="number"
            class="form-control form-control-lg form-code"
            :class="[codeError ? 'is-invalid' : '']"
            id="floatingInput"
            placeholder="000000"
            @input="restrictInput"
            maxlength="6"
            v-model="verificationCode"
          />
          <div class="invalid-feedback">
            Bitte geben Sie den korrekten Code ein.
          </div>
          <br />(dev only: Code ist 123456) <br />
        </div>
        <button
          type="submit"
          class="btn btn-proceed btn-lg w-100 mt-5"
          @click.prevent="checkCode"
          :disabled="verificationCode.toString().length < 6"
        >
          Weiter
        </button>
      </div>
    </form>
  </div>
</template>

<script setup>
import { ref } from "vue";
import { dhRegistrationStore } from "../../../stores/dh-registration";
const store = dhRegistrationStore();
const codeError = ref(false);
const verificationCode = ref("");

function checkCode() {
  if (verificationCode.value !== "123456") {
    codeError.value = true;
  } else {
    store.step++;
  }
}
function restrictInput() {
  verificationCode.value = verificationCode.value.toString().replace(/\D/g, "");
  console.log(verificationCode.value);
}
</script>

<style scoped>
.form-code {
  font-size: 25px;
  text-align: center;
  letter-spacing: 10px;
}
</style>
