<template>
  <h2>Geben Sie bitte diese grundlegenden Daten an:</h2>

  <div class="row my-5">
    <div class="col-6">
      In Teil 1 wird gepr√ºft. ob Sie f√ºr die Studie in Frage kommen.
    </div>
    <div class="col-6 text-center">
      Dauer: 5 Minuten<br />
      <font-awesome-icon
        :icon="['fas', 'clock']"
        class="mt-2"
        style="font-size: 2em"
      />
    </div>
  </div>

  <form
    class="needs-validation"
    @submit.prevent="validateBasicCriteria"
    novalidate
  >
    <div class="form-floating mt-4 mb-4">
      <input
        type="number"
        class="form-control"
        :class="[
          ageError === 1 ? 'is-invalid' : '',
          ageError === 2 ? 'is-valid' : '',
        ]"
        id="floatingInput"
        placeholder="75"
        v-model="store.subject.age"
      />
      <label for="floatingInput">Alter (in ganzen Jahren)</label>
      <span style="font-size: 12px"
        >[dev only: use 68 for age-socialsecurity check later]</span
      >
      <div class="invalid-feedback">
        Bitte geben Sie Ihr korrekter Alter als Zahl ein (z.B. 76).
      </div>
    </div>

    <div class="form-floating mb-4">
      <input
        type="number"
        class="form-control"
        :class="[
          zipError === 1 ? 'is-invalid' : '',
          zipError === 2 ? 'is-valid' : '',
        ]"
        id="floatingInput"
        placeholder="6020"
        v-model="store.subject.zip"
      />
      <label for="floatingInput">Postleitzahl</label>
      <div class="invalid-feedback">
        Bitte geben Sie eine √∂sterreichische Postleitzahl als 4-stellige Zahl an
        (z.B. 6020).
      </div>
    </div>
    <label class="form-label"><h5>Geschlecht:</h5></label>
    <div class="btn-group btn-group-registration w-100" role="group">
      <input
        type="radio"
        class="btn-check"
        id="male"
        value="male"
        v-model="store.subject.sex"
      />
      <label class="btn btn-outline-primary" for="male">M√§nnlich</label>
      <input
        type="radio"
        class="btn-check"
        id="female"
        value="female"
        v-model="store.subject.sex"
      />
      <label class="btn btn-outline-primary" for="female">Weiblich</label>
      <input
        type="radio"
        class="btn-check"
        id="diverse"
        value="diverse"
        v-model="store.subject.sex"
      />
      <label class="btn btn-outline-primary" for="diverse">Divers</label>
    </div>
    <button
      type="submit"
      :disabled="isFormInvalid"
      class="btn btn-proceed btn-lg w-100 mt-5 mb-5"
    >
      Weiter
    </button>
  </form>
</template>

<script setup>
import { ref, computed } from "vue";
import { dhRegistrationStore } from "../../../stores/dh-registration";
import { zipAT } from "../../../assets/zipCodes.js";
const store = dhRegistrationStore();
const ageError = ref(0);
const zipError = ref(0);

const isFormInvalid = computed(() => {
  return !store.subject.age || !store.subject.sex || !store.subject.zip;
});

function validateBasicCriteria() {
  if (
    store.subject.age < 0 ||
    store.subject.age > 150 ||
    store.subject.age !== parseInt(store.subject.age, 10)
  ) {
    ageError.value = 1;
  } else {
    ageError.value = 2;
  }

  if (
    !zipAT.includes(store.subject.zip) ||
    store.subject.zip !== parseInt(store.subject.zip, 10)
  ) {
    zipError.value = 1;
  } else {
    zipError.value = 2;
  }

  if (ageError.value === 2 && zipError.value === 2) {
    store.step++;
  }
}
</script>

<style scoped>
.btn-check + .btn {
  color: #142b58;
  border-color: #142b58;
  height: 50px;
  font-size: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.btn-check:unchecked + .btn {
  color: #142b58;
}
.btn-check:checked + .btn {
  color: white;
  background-color: #142b58;
}
</style>
