<template>
  <h2 class="mb-0">Werfen Sie die M√ºnze!</h2>
  <div ref="animationContainer"></div>
  <button
    @click.prevent="playAnimation"
    :class="[coinTossed ? 'd-none' : '']"
    :disabled="coinTossed"
    class="btn btn-proceed btn-lg w-100"
  >
    M√ºnze werfen
  </button>
</template>

<script>
import lottie from "lottie-web";
import animationData from "/home/mba_wsl/projects/dh-web/src/assets/coinToss.json"; // Replace with your animation JSON file path
import { ref } from "vue";
import { dhRegistrationStore } from "../../../stores/dh-registration";

export default {
  setup() {
    const store = dhRegistrationStore();
    const coinTossed = ref(false);
    const hideButton = () => {
      coinTossed.value = true;
    };
    return { store, coinTossed, hideButton };
  },
  mounted() {
    this.animation = lottie.loadAnimation({
      container: this.$refs.animationContainer,
      renderer: "svg",
      loop: false,
      autoplay: false,
      animationData: animationData,
    });
  },
  methods: {
    playAnimation() {
      this.animation.setSpeed(0.9);
      this.animation.play();
      this.hideButton();
      this.store.subject.cohort = "1"; // this is the "fake" randomisation
      setTimeout(() => {
        this.store.step++;
      }, 4750);
    },
  },
};
</script>

