<template>
  <h2>Unterschrift</h2>
  <h6 v-if="store.osName !== 'windows' || store.osName !== 'unknown'">
    Unterschreiben Sie mit Ihrem Finger in dem unteren Feld.
  </h6>
  <h6 v-else>Unterschreiben Sie mit der Maus in dem unteren Feld.</h6>
  <div class="pad-container">
    <VueSignaturePad
      width="100%"
      height="150px"
      ref="signaturePad"
      class="signature-pad"
      :class="[signatureLocked ? 'locked' : '']"
      :options="{
        onEnd: () => {
          updateSignature();
        },
      }"
    />
  </div>
  <div class="row pad-row justify-content-center mx-0 mb-3">
    <button @click.prevent="clear()" class="btn btn-danger btn-del w-100 py-2">
      <span v-if="signatureLocked"
        ><font-awesome-icon icon="rotate-left" class="px-2" /> Neue Unterschrift
      </span>
      <span v-if="!signatureLocked"
        ><font-awesome-icon icon="trash-can" class="px-2" /> L√∂schen</span
      >
    </button>
  </div>
  <div class="consent-text mb-4">
    Ich erkl√§re mich mit der Teilnahme an dieser Forschungsstudie einverstanden
    und genehmige, dass meine von mir angegebenen pers√∂nlichen Daten korrekt
    sind und verwendet werden. Ich best√§tige, dass ich diese
    Einverst√§ndniserkl√§rung in meinem eigenen Namen ausf√ºlle und dass meine
    elektronische Unterschrift, die durch das Ausf√ºllen des obigen Feldes
    dokumentiert wird, das rechtsverbindliche √Ñquivalent meiner
    handschriftlichen Unterschrift auf dieser Einverst√§ndniserkl√§rung ist.
  </div>
  <button
    type="submit"
    class="btn btn-proceed btn-lg w-100 mt-3"
    @click.prevent="this.store.step++"
    :disabled="!store.subject.signature"
  >
    Speichern & weiter
  </button>
</template>

<script>
import { dhRegistrationStore } from "../../../stores/dh-registration";
import { ref } from "vue";

export default {
  setup() {
    const store = dhRegistrationStore();
    const padEmpty = ref(false);
    const signatureLocked = ref(false);
    const changeButton = (buttonState) => {
      padEmpty.value = buttonState;
    };
    return { store, padEmpty, signatureLocked, changeButton };
  },
  mounted() {
    this.$refs.signaturePad.fromDataURL(this.store.subject.signature);
    if (this.store.subject.signature !== "") {
      this.$refs.signaturePad.lockSignaturePad();
      this.signatureLocked = true;
    }
  },
  name: "SignaturePad",
  methods: {
    clear() {
      this.$refs.signaturePad.clearSignature();
      this.store.subject.signature = "";
      this.$refs.signaturePad.openSignaturePad();
      this.signatureLocked = false;
      this.changeButton(false);
    },
    updateSignature() {
      const { isEmpty, data } =
        this.$refs.signaturePad.saveSignature("image/svg+xml");
      if (!isEmpty) {
        this.store.subject.signature = data;
      }
    },
  },
};
</script>

<style scoped>
.consent-text {
  font-size: 12px;
}
.pad-container {
  border: double 2px black;
  text-align: center;
  vertical-align: center;
}

.locked {
  background-color: rgb(233, 233, 233);
}

.btn-del {
  border-radius: 0 0 0.375rem 0.375rem;
  font-size: 18px;
}
</style>
