<template>
  <form class="needs-validation" @submit.prevent="checkPassword" novalidate>
    <h2>E-Mail best√§tigt</h2>
    <div class="text-center">
      Danke, Ihre E-Mail-Addresse wurde erfolgreich best√§tigt. <br /><br />Bitte
      erstellen Sie nun ein <b>Passwort</b>:
      <div class="input-group my-4">
        <div class="form-floating">
          <input
            :type="passwordHidden ? 'password' : 'text'"
            class="form-control"
            v-model="passwordText"
            placeholder="123456"
          />
          <label for="floatingInput">Passwort</label>
        </div>

        <button
          class="btn btn-outline-secondary"
          type="button"
          @click="passwordHidden = !passwordHidden"
        >
          <font-awesome-icon
            :icon="passwordHidden ? ['fas', 'eye'] : ['fas', 'eye-slash']"
          />
        </button>
      </div>
      <div class="input-group my-4">
        <div class="form-floating">
          <input
            :type="confirmHidden ? 'password' : 'text'"
            class="form-control"
            v-model="confirmText"
            placeholder="123456"
          />
          <label for="floatingInput">Passwort best√§tigen</label>
        </div>

        <button
          class="btn btn-outline-secondary"
          type="button"
          @click="confirmHidden = !confirmHidden"
        >
          <font-awesome-icon
            :icon="confirmHidden ? ['fas', 'eye'] : ['fas', 'eye-slash']"
          />
        </button>
      </div>
    </div>
    <div class="text-danger" :class="passwordError < 1 ? 'd-none' : ''">
      <span v-if="passwordError === 1"
        >Das Passwort muss zumindesten 8 Stellen lang sein und 1 Buchstaben und
        1 Zahl enthalten.</span
      >
      <span v-if="passwordError === 2"
        >Die beiden Passw√∂rter stimmen nicht √ºberein.</span
      >
    </div>
    <button
      type="submit"
      class="btn btn-proceed btn-lg w-100 mt-5"
      @click.prevent="checkPassword"
      :disabled="!passwordText || !confirmText"
    >
      Weiter
    </button>
  </form>
</template>

<script setup>
import { dhRegistrationStore } from "../../../stores/dh-registration";
const store = dhRegistrationStore();
import { ref } from "vue";

const passwordError = ref(0);
const passwordHidden = ref(true);
const confirmHidden = ref(true);
const passwordText = ref("");
const confirmText = ref("");

function checkPassword() {
  const passwordRegex = /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/gm;
  if (!passwordRegex.test(passwordText.value)) {
    passwordError.value = 1;
  } else if (passwordText.value !== confirmText.value) {
    passwordError.value = 2;
  } else {
    store.subject.password = passwordText.value;
    store.step++;
  }
}
</script>

<style scoped>
.input-group-addon i {
  margin-left: -30px;
  cursor: pointer;
  z-index: 200;
  position: absolute;
  font-size: large;
  color: #6c757d;
}
.display-eye {
  cursor: pointer;
}
</style>
