<template>
  <div class="fixed-bottom pt-4 pb-3 px-3 text-center bg-white">
    <div class="progress mb-2">
      <div
        class="progress-bar progress-bar-striped"
        role="progressbar"
        :style="{ width: progress + '%' }"
      ></div>
    </div>
    <span class="text-dark">Fortschritt</span>
  </div>
</template>

<script setup>
import { computed } from "vue";
import { dhRegistrationStore } from "../../../stores/dh-registration";

const store = dhRegistrationStore();
const maxSteps =
  require.context("./../screens", false, /\.(vue)$/).keys().length - 3;

const progress = computed(() => {
  return (store.step / maxSteps) * 100;
});
</script>

<style scoped>
.progress {
  height: 20px;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}
.progress-bar {
  background-color: #142b58;
}

.fixed-bottom {
  box-shadow: 0px 4px 10px 1px black;
}
</style>
