<script setup>
import { ref } from "vue";
import ProgressBar from "./registration_components/utils/ProgressBar.vue";
import WelcomeScreen from "./registration_components/screens/WelcomeScreen.vue";
import BasicCriteriaScreen from "./registration_components/screens/BasicCriteriaScreen.vue";
import BasicConfirmationScreen from "./registration_components/screens/BasicConfirmationScreen.vue";
import QuestionStartScreen from "./registration_components/screens/QuestionStartScreen.vue";
import QuestionAfibScreen from "./registration_components/screens/QuestionAfibScreen.vue";
import QuestionAnticoagulantScreen from "./registration_components/screens/QuestionAnticoagulantScreen.vue";
import QuestionHypertensionScreen from "./registration_components/screens/QuestionHypertensionScreen.vue";
import QuestionHeartfailureScreen from "./registration_components/screens/QuestionHeartfailureScreen.vue";
import QuestionAngiopathyScreen from "./registration_components/screens/QuestionAngiopathyScreen.vue";
import QuestionStrokeScreen from "./registration_components/screens/QuestionStrokeScreen.vue";
import QuestionDiabetesScreen from "./registration_components/screens/QuestionDiabetesScreen.vue";
import QuestionConfirmationScreen from "./registration_components/screens/QuestionConfirmationScreen.vue";
import ConsentStartScreen from "./registration_components/screens/ConsentStartScreen.vue";
import ConsentVideoScreen from "./registration_components/screens/ConsentVideoScreen.vue";
import ConsentDocumentScreen from "./registration_components/screens/ConsentDocumentScreen.vue";
import ConsentSignatureScreen from "./registration_components/screens/ConsentSignatureScreen.vue";
import AbortScreen from "./registration_components/screens/AbortScreen.vue";
import CancelModal from "./registration_components/utils/CancelModal.vue";
import ConsentDataScreen from "./registration_components/screens/ConsentDataScreen.vue";
import EmailConfirmationScreen from "./registration_components/screens/EmailConfirmationScreen.vue";
import EmailPasswordScreen from "./registration_components/screens/EmailPasswordScreen.vue";
import RiskStartScreen from "./registration_components/screens/RiskStartScreen.vue";
import RiskHeightWeightScreen from "./registration_components/screens/RiskHeightWeightScreen.vue";
import RiskCHDScreen from "./registration_components/screens/RiskCHDScreen.vue";
import RiskMyocardialScreen from "./registration_components/screens/RiskMyocardialScreen.vue";
import RiskPacemakerScreen from "./registration_components/screens/RiskPacemakerScreen.vue";
import RiskLungScreen from "./registration_components/screens/RiskLungScreen.vue";
import RiskRenalScreen from "./registration_components/screens/RiskRenalScreen.vue";
import RiskVertigoScreen from "./registration_components/screens/RiskVertigoScreen.vue";
import RiskBleedingScreen from "./registration_components/screens/RiskBleedingScreen.vue";
import RiskDyspnoeaScreen from "./registration_components/screens/RiskDyspnoeaScreen.vue";
import RiskMedicationScreen from "./registration_components/screens/RiskMedicationScreen.vue";
import RiskSmokingScreen from "./registration_components/screens/RiskSmokingScreen.vue";
import RiskAlcoholScreen from "./registration_components/screens/RiskAlcoholScreen.vue";
import RiskCancerScreen from "./registration_components/screens/RiskCancerScreen.vue";
import RiskActivityScreen from "./registration_components/screens/RiskActivityScreen.vue";
import RandomStartScreen from "./registration_components/screens/RandomStartScreen.vue";
import RandomCoinScreen from "./registration_components/screens/RandomCoinScreen.vue";
import RandomEndScreen from "./registration_components/screens/RandomEndScreen.vue";
import LockScreen from "./registration_components/screens/LockScreen.vue";
import { dhRegistrationStore } from "../stores/dh-registration";
const store = dhRegistrationStore();
const abortError = ref(0);

function next() {
  store.step++;
  if (store.step === 3) {
    checkAgeAbort();
  } else if (store.step === 12) {
    checkCriteriaAbort();
  }
}

function checkAgeAbort() {
  if (store.subject.age < 65) {
    abortError.value = 1;
    store.step = 0;
  }
}

function checkCriteriaAbort() {
  if (store.criteria.afib === true || store.criteria.anticoagulant !== "none") {
    abortError.value = 2;
    store.step = 0;
  }
}
</script>

